<template lang="html">
    <div class="todo-input">
    <div class="ui grid">
        <div class="seven wide column ui input">
          <input type="text" v-model="text"/>
          <button class="ui positive button" v-on:click="add">Agregar</button>
        </div>
        <div class="three wide column"></div>
        <div class="six wide column">
          <div style="margin-left: 20px">Sube un archivo: <input type="hidden" role="uploadcare-uploader" name="my_file" />
        </div>
        </div>

      </div>


</div>
</template>

<script>
    export default {
        name: 'todo-input',
        props: ['todo'],
        data(){
            return { text: "" };
        },
        methods: {
            add() {
                this.$emit('add:new',this.text);
                this.text = "";
            }
        }
    }
</script>


