<template lang="html">
    <div class="wrapper" >
            <div style="font-size: 40px;margin: 20px 0 20px 0">{{todo.text}}</div><button style="margin-bottom: 5px" class="ui negative little button" v-on:click="remov">Remover</button><button style="margin-bottom: 5px" class="ui positive little button" v-on:click="terminado">Terminado</button> <input type="hidden" role="uploadcare-uploader" name="my_file" />
    </div>
    </template>

<script>
    export default {
        name: 'todo-item',
        props: ['todo'],
        methods: {
            remov(){
                this.$emit('todo:remov',this.todo.id);
            },
            terminado() {
                this.$emit('terminado',this.todo.text,this.todo.id);
            }
        }
    }
</script>

<style>
    .wrapper {
        width: 900px;
        border: 3px solid rgba(100,100,100,0.2);
        padding: 5px 0 0 5px;
        text-align: left;
        margin: 0 0 0 -22px;
    }
</style>


