import AppHeader from './components/AppHeader.vue';
import todoInput from './components/todoInput.vue';
import todoItem from './components/todoItem.vue';

export default {
    name: 'App',
    components: {
        AppHeader,
        todoInput,
        todoItem
    },
    data(){
        return {
          todos: [ ],
          nextId: 0
        };
    },
    methods: {
        add(text) {
            let todos = this.todos;
            let currentId = 5;
            database.ref('lista').once('value',function (snapshot) {
                currentId = snapshot.numChildren();
                database.ref('lista').push({id: currentId,text: text});
                let query = database.ref('lista').orderByKey();
                query.once("value")
                    .then(function(snapshot) {
                        snapshot.forEach(function(childSnapshot) {
                            let data = childSnapshot.val();
                            let key = childSnapshot.key;
                            if (data.text==text){
                                database.ref('lista/'+key).set({id: key,text: text});
                                todos.push({id: key, text: text});
                            }
                        });
                    });
            });
        },
        remov(id) {
            let todos = this.todos;
            let query = database.ref('lista').orderByKey();
            query.once("value")
                .then(function(snapshot) {
                    snapshot.forEach(function(childSnapshot) {
                        let data = childSnapshot.val();
                        console.log(data.id);
                        let key = childSnapshot.key;
                        if (data.id==id){
                            database.ref('lista/'+key).remove();
                        }
                    });
                });
            this.todos = todos.filter((todo) => todo.id != id);
        },
        terminado(text, id) {
            this.remov(id);
            this.add("Terminado - " + text);
            axios
                .get('https://api.giphy.com/v1/gifs/random?api_key=mx7F9MmF9mhPITAxiVoqE4rKRc0KxII7&limit=1')
                .then(response => (document.getElementById('myImg').src = response.data.data.images.fixed_width.url));

        },
        inicio() {
            let todos=this.todos;
            let query = database.ref('lista').orderByKey();
            query.once("value")
                .then(function(snapshot) {
                    snapshot.forEach(function(childSnapshot) {
                        todos.push(childSnapshot.val());
                    });
                });
        }
    },
    beforeMount(){
        this.inicio();
    }
}



